.logo {
  display: block;
  width: 55px;
  height: 61px;
  font-family: $montserrat;
  font-size: 30px;
  font-weight: 700;
  line-height: 30px;
  text-align: center;
  text-transform: uppercase;
  color: $black;

  &--header {
    position: absolute;
    top: 10px;
    left: 50%;
    transform: translateX(-50%);

    @include onDesktop {
      @include hover(top, 20px);
    }
  }

  &--footer {
    display: block;

    @include onDesktop {
          @include hover(transform, scale(110%));
    }
  }

  @include onTablet {
    top: 15px;
    left: 54%;
    transform: translateX(0);
    width: 80px;
    height: 72px;
    font-size: 36px;
  }
}
